import React, { FC, useState, useLayoutEffect, CSSProperties, useRef } from 'react'
import cls from 'classnames'
import styles from './index.module.scss'
import { timestampToDate } from '@/utils/utils'
import i18n from '@/utils/i18n';
import { Image, View } from '@tarojs/components';

interface IProps {
  children?: any
  timestamp: any
  className: string
  style?: CSSProperties
  version?: string
  edition?: number
}

const aImgs = {
  ordinary: require('@/common/img/icon_oridinary.png'),
  advanced: require('@/common/img/icon_advanced.png'),
  profession: require('@/common/img/icon_profession.png')
}

export const remainingTime = (timestamp) => {
  const date = new Date()
  const time = timestamp * 1 - date.getTime()
  const diff = Math.floor(time / (1000 * 3600 * 24))

  return diff
}

const TimeLeft: FC<IProps> = ({ edition, timestamp, className, ...props }) => {
  const t = i18n();
  return (
    <View className={cls(styles.timeLeft)} {...props}>
      {edition !== 1 && t('useTime1').replace(/\s/g, ' ')}
      {t('useTime2', { day: remainingTime(timestamp) }).replace(/\s/g, ' ')}
    </View>
  )
}

const TimeLeftTip: FC<IProps> = ({ ...props }) => {
  const { timestamp, className, children, version = '' } = props
  const [visible, setVisble] = useState(false)
  const [rect, setRect] = useState({
    top: 0,
    left: 0
  })
  const imgEl = useRef<HTMLElement>(null)
  const t = i18n();

  const handleVisibleChange = () => {
    setVisble(!visible)
  }

  useLayoutEffect(() => {
    if (!imgEl.current) return
    const m = {
      top: imgEl?.current?.offsetTop,
      left: imgEl?.current?.offsetLeft
    }
    setRect(m)
  }, [])

  return (
    <View className={cls(styles.timeLeftTip, className)}>
      {children}
      {timestampToDate(timestamp)}
      {t('expire')}
      <Image
        className={styles.tipImg}
        src={aImgs[version]}
        onClick={handleVisibleChange}
        ref={imgEl}
      />

      <View
        className={`${styles.popover} ${visible && styles.visible}`}
        style={{ top: `${rect?.top + 22}px` }}
      >
        <View className={styles.arrow} style={{ left: `${rect?.left + 3}px` }} />
        <View className={styles.inner}>
          <View className={styles.innerWrapper}>
            <View className={styles.content}>{t('expireDesc').replace(/\s/g, ' ')}</View>
          </View>
        </View>
      </View>
    </View>
  )
}

export { TimeLeft, TimeLeftTip }
