import React, { useState } from 'react';
import { View, Button } from '@tarojs/components'
import Taro from '@tarojs/taro'
import styles from './index.module.scss'
import { request } from '@/utils/request';

const Test: React.FC = () => {
    const [data, setData] = useState()

    /** 请求atop测试 */
    const getAtopDataTest = async () => {
        console.log('start getAtopDataTest')
        const res = await request({
            // 
            // url: 'tuya.m.cl.combo.merchant.detail',
            // url: 'tuya.m.cl.combo.register'
            url: 'tuya.m.cl.project.list',
            data: {
                networkType: -1
            },
            apiVersion: '4.0'
        })
        console.log('getAtopDataTest', res)
        setData(data)
    }

    /** 测试小程序多语言 */
    const registerSuccess = () => {
        ty.navigateTo({
            url: '/pages/registerSuccess/index'
        })
    }

    /** 测试小程序退出 */
    const logout = () => {
        try {
            ty.lamp.logOut({
                success: (res) => {
                    console.log('测试小程序退出-success', res)
                    Taro.showToast({ title: '退出成功' })
                },
                fail: (err) => {
                    console.error('测试小程序退出-fail', err)
                    Taro.showToast({ title: '失败fail' })
                }
            })
        } catch (error) {
            Taro.showToast({ title: '失败catch' })
            console.error('测试小程序退出-catch', error)
        }

    }

    const testList = [
        { name: '退出登录测试', event: logout },
        { name: '测试ios手势返回', event: registerSuccess }
    ]

    return (<View className={styles.name}>
        {testList?.map((v) => <Button key={v.name} className={styles.btn} onClick={v.event}>{v.name}</Button>)}
    </View>)
}
export default Test
