import React from 'react';
import BaseComponentProps from '@/common/types/BaseComponentProps';
import { View } from '@tarojs/components';
import { AtButton } from 'taro-ui';
import styles from './index.module.scss';
import { isAndroid } from '@/utils/getSystemInfoSync';

export interface BaseOperateFooterProps extends BaseComponentProps {
  primaryButtonText: string;
  onPrimaryButtonClick: () => any;
  secondaryButtonText?: string;
  onSecondaryButtonClick?: () => any;
  primaryButtonLoading?: boolean;
  primaryButtonDisabled?: boolean;
  secondaryButtonLoading?: boolean;
  secondaryButtonDisabled?: boolean;
}
const BaseOperateFooter: React.FC<BaseOperateFooterProps> = ({
  className,
  primaryButtonText,
  secondaryButtonText,
  onPrimaryButtonClick,
  onSecondaryButtonClick,
  primaryButtonLoading = false,
  primaryButtonDisabled = false,
  secondaryButtonLoading = false,
  secondaryButtonDisabled = false
}) => (
  <View className={`${styles.baseOperateFooter}  ${isAndroid ? styles.isAndroidBaseOperateFooter : styles.isIOSBaseOperateFooter}  ${className}`}>
    {secondaryButtonText && (
      <AtButton
        className={styles.secondaryBtn}
        type='secondary'
        onClick={onSecondaryButtonClick}
        loading={secondaryButtonLoading}
        disabled={secondaryButtonDisabled}
        circle
      >
        {secondaryButtonText}
      </AtButton>
    )}
    <AtButton
      type='primary'
      circle
      onClick={onPrimaryButtonClick}
      className={secondaryButtonText ? '' : styles.large}
      loading={primaryButtonLoading}
      disabled={primaryButtonDisabled}
    >
      {primaryButtonText}
    </AtButton>
  </View>
);

export default BaseOperateFooter;
