import React, { FC, CSSProperties } from 'react'
import cls from 'classnames'
import styles from './index.module.scss'
import { Image, View } from '@tarojs/components'
import imgEmpty from '@/img/img_empty.png'

// icon图片组件
interface IIMGProps {
  className?: string
  style?: CSSProperties
  src: string
  bgColor?: string
}

// 文本组件
interface ITxtProps {
  className?: string
  style?: CSSProperties
  title?: string
}


// icon图片文本组合组件
interface IIMGItemProps {
  imgConfig?: IIMGProps
  className?: string
  style: CSSProperties
  justify: 'start' | 'end' | 'center' | 'space-around' | 'space-between'
  txtConfig: ITxtProps
}

const ImgItem: FC<Partial<IIMGItemProps>> = ({
  justify = 'center',
  style,
  className,
  imgConfig,
  txtConfig,
  // tslint:disable-next-line:trailing-comma
  ...props
}) => {
  // const { justify = 'center', style, className, imgConfig, txtConfig } = props
  const styleConfig = {
    alignItems: justify,
    ...style
  }

  const onError = (e) => {
    console.log('onError', e)
  }

  const cl = cls(styles.wrapper, className)
  return (
    <View className={cl} style={styleConfig} {...props}>
      <Image imgProps={{alt: 'error'}} onError={onError} className={styles.img} defaultSource={imgEmpty} src={imgConfig?.src} style={styleConfig} />
      <View {...txtConfig} className={styles.txt}>{txtConfig?.title}</View>
    </View>
  )
}

export default ImgItem
