import { PropsWithChildren, useEffect } from 'react';
import Taro, { useLaunch, usePageNotFound } from '@tarojs/taro';
import { GLOBAL_KEY, setGlobalData } from './config/globalData';
import { getLanguage } from './utils/getLanguage';
import 'taro-ui/dist/style/index.scss'; // 引入taro-ui组件样式
import './app.scss';

function App({ children }: PropsWithChildren) {
  /** 等同于 App 入口的 onLaunch 生命周期钩子  */
  useLaunch(() => {
    console.log('App launched.');
    getLanguage();
  });

  useEffect(() => {
    try {
      ty.getNetworkType({
        success: (res) => {
          console.log('getNetworkType: ', res);
          setGlobalData(GLOBAL_KEY.IS_ONLINE, res.networkType !== 'none');
        }
      });
      ty.onNetworkStatusChange((res) => {
        console.log('onNetworkStatusChange: ', res);
        setGlobalData(GLOBAL_KEY.IS_ONLINE, res.networkType !== 'none');
      });
    } catch (e) {
      console.log(e);
    }
  }, []);

  /** notFound默认页面  */
  usePageNotFound(() => {
    // 如果是 tabbar 页面，请使用 Taro.switchTab
    Taro.redirectTo({
      url: 'pages/index/index'
    });
  });

  // children 是将要会渲染的页面
  return children;
}

export default App;
