import mockData from '@/api/mockData';
import { GLOBAL_KEY, getGlobalData } from '../../config/globalData/index';
import Taro from '@tarojs/taro';

export interface IResponse<T = any> {
  result: T;
  success: boolean;
  code: number;
  status: string;
  msg?: string;
  errorCode?: string;
}

type Method = 'GET' | 'POST' | 'PUT' | 'DELETE';

interface SelectUrl {
  /**
   * @name atop接口（不走fast网关）
   */
  atopUrl?: string;

  /**
   * @name atop接口（走fast网关）
   */
  atopUrlByFast?: string;
}

interface ExtraConfig {
  noBlockId?: boolean;
  noErrorToast?: boolean;
  noRedirect?: boolean;
  ignoreErrorCodes?: number[];
  noLogin?: boolean;
}
export async function request<T = any>(reqConfig: {
  url: string;
  method?: Method;
  data?: Record<string, any>;
  extData?: { gid?: string };
  apiVersion?: string;
  extraConfig?: ExtraConfig;
}): Promise<IResponse<T>> {
  const { url, data, apiVersion, extData } = reqConfig;
  const isOnline = getGlobalData(GLOBAL_KEY.IS_ONLINE);
  if (!isOnline) {
    Taro.hideLoading();
    Taro.showToast({ title: 'Network unavailable', duration: 2000, icon: 'none' });
    return Promise.reject({});
  }

  let res: any = { result: {}, success: false, code: 0 };

  const brand = 'devtools' // devtools

  const requestAtopDirectly = () =>
    new Promise((resolve) => {
      // IDE调试工具
      if (brand !== 'devtools') {
        resolve({
          data: {
            result: mockData?.[url] || {},
            success: true
          }
        });
        return;
      }

      ty.apiRequestByAtop({
        api: url,
        version: apiVersion || '1.0',
        postData: { ...data } || {},
        extData,
        success: (result) => {
          console.log('------------- result  ↓  -------------------');
          console.log('url: ', url);
          console.log('params: ', data);
          console.log('result', result);
          console.log('---------------  ↑ ------------------------');
          resolve({
            data: {
              result,
              success: true
            }
          });
        },
        fail: (e) => {
          console.log('fail: ', e);
          resolve({
            data: Object.assign({}, e?.innerError, { code: +e?.innerError?.errorCode }),
            errorMsg: e?.errorMsg
          });
        },
        complete: () => {
          Taro.hideLoading();
        }
      });
    });

  res = await requestAtopDirectly();

  if (res?.data?.success) {
    const { data: resData } = res;
    const { result = {}, success, code, msg } = resData;
    res = { result, success, code, msg };

    console.log('res', resData);
  } else {
    console.log('---error----');
    console.log(res);
    setTimeout(() => {
      Taro.showToast({ title: res.data.errorMsg, duration: 2000, icon: 'none' });
    }, 200);
  }
  return res;
}
