export enum GLOBAL_KEY {
  /** 网络状态*/
  IS_ONLINE = 'IS_ONLINE',
  ADD_VALUE = 'ADD_VALUE',
}

let MpGlobalData = {
  /** 网络状态*/
  [GLOBAL_KEY.IS_ONLINE]: true,
  [GLOBAL_KEY.ADD_VALUE]: {} as any
};

export function setGlobalData<K extends keyof typeof MpGlobalData>(key: K, val) {
  if (!key) {
    console.error('key值不能为空');
    return;
  }
  MpGlobalData[key] = val;
}

export function getGlobalData<K extends keyof typeof MpGlobalData>(key: K) {
  return MpGlobalData[key];
}

export const clearGlobalData = () => {
  if (MpGlobalData) {
    MpGlobalData = {} as typeof MpGlobalData;
  }
}
