import { View, Image, Input } from '@tarojs/components';
import { useThrottleFn } from 'ahooks';
import React, { useState } from 'react';
import styles from './index.module.scss';

export interface SearchBarProps {
  /**
   * @description 输入框的placeholder
   * @default 请输入
   */
  placeholder?: string;
  /**
   * @description 输入完点击确认时触发
   */
  onSearch?: (value: string) => void;
  /**
   * @description 当取消搜索时
   */
  onCancel?: () => void;
  /**
   * @description 搜索框输入的字数限制
   * @default 20
   */
  maxLength?: number;
  /**
   * @description 是否在输入时自动搜索;设置具体数值时，当输入的字符串大于该字符自动搜索
   * @default false
   */
  searchOnChange?: boolean | number;
  /**
   * @description 清空时是否触发搜索
   * @default false
   */
  searchOnClear?: boolean;
  /**
   * @description  获取焦点
   * @default false
   */
  focus?: boolean;
  /**
   * @description  是否固定在顶部
   * @default false
   */
  fixed?: boolean;
}

const SearchBar: React.FC<SearchBarProps> = ({
  placeholder = '',
  onSearch,
  // onCancel,
  maxLength = 20,
  searchOnChange = false,
  searchOnClear = false,
  focus = false,
  fixed = false
}) => {
  const [value, setValue] = useState<string>('');
  const { run: onInput } = useThrottleFn(
    (e) => {
      setValue(e.detail.value.substring(0, maxLength));
      if (e.detail.value.length <= maxLength && searchOnChange) {
        // 自动搜索-分行写看起来清晰一点
        // 当前字数大于自动搜索需要的最小数值
        // const number =
        //   typeof searchOnChange === 'number' && searchOnChange > 0 ? searchOnChange : 0;
        // e.detail.value.length >= number &&  onSearch?.(e.detail.value);
        
        onSearch?.(e.detail.value);
      }
    },
    { wait: 300 }
  );
  return (
    <View className={`${styles.searchBarWrapper} ${fixed ? styles.searchFixed : ''}`}>
      <View className={styles.searchContainer}>
        <Image className={styles.searchIcon} src={require('@/img/icon_search.svg')} />
        <Input
          className={styles.searchInput}
          placeholder={placeholder}
          confirmType='search'
          placeholderClass={styles.inputPlaceholder}
          onConfirm={() => {
            onSearch && onSearch(value);
          }}
          value={value}
          onInput={onInput}
          maxlength={maxLength}
          focus={focus}
        />
        {value && (
          <Image
            className={styles.deleteIcon}
            src={require('@/img/icon_delete_N4.svg')}
            onClick={() => {
              setValue('');
              searchOnClear && onSearch?.('');
            }}
          />
        )}
      </View>
    </View>
  );
};
export default SearchBar;
