import i18n from '@/utils/i18n';
import BaseComponentProps from '@/common/types/BaseComponentProps';
import { View } from '@tarojs/components';
import React, { ReactElement } from 'react';
import { AtModal, AtModalAction, AtModalContent, AtModalHeader } from 'taro-ui';
import styles from './index.module.scss';

export interface BaseModalProps extends BaseComponentProps {
  isOpened: boolean;
  title: string;
  content?: string;
  customContent?: ReactElement;
  confirmText?: string;
  cancelText?: string;
  onConfirm: () => void;
  onCancel?: () => void;
  showCancel?: boolean;
  className?: any;
}
const t = i18n();

const BaseModal: React.FC<BaseModalProps> = ({
  isOpened,
  title,
  content,
  customContent = undefined,
  onConfirm,
  showCancel = false,
  onCancel,
  confirmText = t('ok'),
  cancelText = t('cancel'),
  className
}) => {
  const handleCancelClick = (e) => {
    e.stopPropagation();
    onCancel?.();
  };

  return (
    <View className={`${styles.wrapper} ${className}`}>
      {!customContent && (
        <AtModal
          className={`${styles.baseModal} ${styles.baseModalIndex} ${content ? '' : styles.withoutContent}`}
          isOpened={isOpened}
          title={title}
          confirmText={confirmText}
          cancelText={showCancel ? cancelText || t('cancel') : undefined}
          content={content}
          onConfirm={onConfirm}
          onCancel={handleCancelClick}
          closeOnClickOverlay={false}
        />
      )}
      {customContent && (
        <AtModal
          isOpened={isOpened}
          className={`${styles.baseModal} ${styles.baseModalIndex} ${className}`}
          onConfirm={onConfirm}
          onCancel={handleCancelClick}
          closeOnClickOverlay={false}
        >
          <AtModalHeader>{title}</AtModalHeader>
          <AtModalContent>{customContent}</AtModalContent>
          <AtModalAction>
            <View className={'at-modal-footer'}>
              {showCancel && (
                <View className={'at-modal-footer-cancel'} onClick={handleCancelClick}>
                  {cancelText}
                </View>
              )}
              <View className={'at-modal-footer-confirm'} onClick={onConfirm}>
                {confirmText}
              </View>
            </View>
          </AtModalAction>
        </AtModal>
      )}
    </View>
  );
};

export default BaseModal;
