import React, { useLayoutEffect, useState } from 'react'
import i18n from '@/utils/i18n';
import styles from './index.module.scss'
import { Button, Image, View } from '@tarojs/components';
import Empty from '@/components/Empty';
import { getMerchantDetail } from '@/api/index';
import { isEmpty } from 'lodash';
import ComboHead from '../ComboHead';
import Container from '@/components/container';
import Box from '@/components/Box';
import ImgItem from '@/components/ImgItem';
import arrowImg from '@/img/arrow@2x.png'
import Taro from '@tarojs/taro';
import { GLOBAL_KEY, setGlobalData } from '@/config/globalData';

const BaseModule: React.FC = () => {
  const t = i18n();

  const [data, setData] = useState({})

  const getMerchantDetailFn = async () => {
    try {
      Taro.showLoading({ title: t('loading') })
      const res = await getMerchantDetail()
      setData(res)
      Taro.hideLoading()
    } catch (error) {
      Taro.hideLoading()
    }
  }

  useLayoutEffect(() => {
    console.log('getMerchantDetailFn')
    getMerchantDetailFn()
  }, [])


  const handelClick = (value) => {
    setGlobalData(GLOBAL_KEY.ADD_VALUE, value)
    Taro.navigateTo({ url: '/pages/addValue/index?id=' + value.id })
  }

  const jumpChoose = () => {
    const version = data?.versionInfo?.map((v) => v?.versionCode)
    console.log(`pages/choose/index?version=${version?.join(',')}&method=change`)
    Taro.navigateTo({ url: `/pages/choose/index?version=${version?.join(',')}&method=change` })
  }


  return <Empty isEmpty={isEmpty(data)}>
    <ComboHead data={data} className={styles.head} />

    <Container className={`${!data?.oem && styles.version} ${styles.pd}`}>
      {(data?.addValueList || []).length > 0 && (
        <Box className={styles.combo}>
          <View className={styles.nav}>
            <View className={styles.title}>{t('addValue')}</View>
          </View>
          {(data?.addValueList || []).map((item) => (
            <View
              onClick={() => handelClick(item)}
              key={item?.id}
              className={styles.appreciation}
            >
              <ImgItem
                txtConfig={{
                  title: item?.name
                }}
                imgConfig={{
                  src: item?.pic
                }}
              />

              <Image
                className={styles.nextIcon}
                src={arrowImg}
              />
            </View>
          ))}
        </Box>
      )}
      {(!data?.oem && data?.edition === 0) && (
        <View className={styles.buttonBg}>
          <Button
            onClick={jumpChoose}
            className={`${styles.button} ${styles[data?.comboSingleDetailVO?.version]}`}
          >
            {t('seeAll')}
          </Button>
        </View>
      )}
    </Container>

  </Empty>
}

export default BaseModule
