import React, { useLayoutEffect, useMemo, useState } from 'react'
import Taro, { getCurrentInstance, useDidShow } from '@tarojs/taro';
import { getComboList, postUserCombo } from '@/api/index';
import i18n from '@/utils/i18n';
import styles from './index.module.scss'
import VersionContent from './VersionContent';
import { AtTabs, AtTabsPane } from 'taro-ui'
import BasePage from '@/components/BasePage';
import BaseModal from '@/components/BaseModal';
import { View } from '@tarojs/components';
import { isEmpty } from 'lodash';

const Choose = () => {
  const t = i18n();
  const { router } = getCurrentInstance();
  const { version = 'ordinary', method = 'chose' } = router?.params || {};

  console.log('version: ', version)
  const [versionList, setVersionList] = useState<string[]>([])
  const [tabs, setTabs] = useState([])
  const [visible, setVisible] = useState(false)
  const [data, setData] = useState<{ version: any, [key: string]: any }[]>([])

  const [curVersion, setCurVersion] = useState(0)

  // 获取套餐详情
  const getComboListData = async () => {
    const res = await getComboList()
    setData(res)

    // 更新tabs
    if (res) {
      setTabs(res?.map((v) => ({
        title: v.name
      })))
    }
  }

  useDidShow(() => {
    ty.hideMenuButton()
    // ty.hideHomeButton()
    getComboListData()
    // ty.setNavigationBarBack({ type: 'custom' })
  })

  useLayoutEffect(() => {
    const title = method === 'change' ? t('allCombo') : t('choseTitle')
    Taro.setNavigationBarTitle({ title })
  }, [])

  useLayoutEffect(() => {
    setVersionList(version?.split(','))
  }, [version])

  const handleClick = (v) => {
    setCurVersion(v)
  }

  const curVersionData = useMemo(() => {
    if (isEmpty(data)) return
    const curVersionStr = data?.map((v) => v.version)
    const currentVersionItem = data?.[curVersion]
    console.log(curVersionStr, curVersion, version)
    if (method === 'change') {
      currentVersionItem.buttonWord = versionList?.includes(curVersionStr[curVersion]) ? t('using') : ''
    }

    currentVersionItem.curVersionStr = curVersionStr
    return currentVersionItem
  }, [data, versionList, curVersion, method])

  /**
   * 免费试用15天按钮提交
   */
  const onPrimaryButtonClick = async () => {
    // 不可试用的用户，显示弹出联系业务经理
    if (!curVersionData?.trail) {
      setVisible(true)
      return
    }
    Taro.showLoading({ title: t('loading') })
    const res = await postUserCombo({
      version: curVersionData?.version
    })
    Taro.hideLoading()
    if (res) {
      Taro.showToast({ title: t('optionSuccess') })
      ty.exitMiniProgram()
    }
  }

  return <BasePage
    footerConfig={curVersionData?.buttonWord ? {
      primaryButtonText: curVersionData?.buttonWord,
      primaryButtonDisabled: method === 'change' && versionList?.includes(curVersionData?.curVersionStr?.[curVersion]),
      onPrimaryButtonClick
    } : void 0}
  >
    <AtTabs current={curVersion} scroll tabList={tabs} onClick={handleClick}>
      {data?.map((item, index) => <AtTabsPane key={item.version} current={curVersion} index={index}>
        <VersionContent key={item?.version || index} data={item} index={index} />
      </AtTabsPane>)}
    </AtTabs>

    <BaseModal
      isOpened={visible}
      customContent={<View>
        <View className={styles.customContent}>{t('contacts')}：{curVersionData?.contactPerson.name}</View>
        <View className={styles.customContent}>{t('email')}：{curVersionData?.contactPerson.email.replace(/\s/g, ' ')}</View>
        <View className={styles.customContent}>{t('contactInformation')}：{curVersionData?.contactPerson.phone}</View>
      </View>}
      title={t('btnConsult').replace(/\s/g, ' ')}
      onConfirm={() => setVisible(false)}
      showCancel={false}
    />
  </BasePage>
}

export default Choose
