import React, { CSSProperties } from 'react';
import BaseComponentProps from '@/common/types/BaseComponentProps';
import { Image, View } from '@tarojs/components';
import NoDataImg from '@/img/noData.png'
import styles from './index.module.scss';

interface BaseEmpty extends BaseComponentProps {
  image?: string
  description?: string
  imageClass?: CSSProperties
}

const BaseEmpty: React.FC<BaseEmpty> = (props) => {
  const { className, style, image, imageClass, description = "No data" } = props
  const emptyImg = image ?? NoDataImg
  return <View className={`${styles.baseEmpty} ${className}`} style={style}>
    <Image src={emptyImg} className={`${styles.image} ${imageClass}`}></Image>
    <View className={styles.description}>{description}</View>
  </View>
};

export default BaseEmpty;
