/* 时间戳转日期 */

export function timestampToDate(value) {
  const date = new Date(value);
  const Y = `${date.getFullYear()}-`;
  const M = `${date.getMonth() + 1 < 10 ? `0${date.getMonth() + 1}` : date.getMonth() + 1}-`;
  const D = `${date.getDate()} `;
  // h = date.getHours() + ':'
  // m = date.getMinutes() + ':'
  // s = date.getSeconds()
  // console.log(Y + M + D + h + m + s) //呀麻碟
  return Y + M + D;
}
