import { request } from '@/utils/request';

/**
 * @name 获取登陆企业信息
 * @api tuya.m.cl.combo.register
 * @version 1.0
 */
export const getRegisterInfo = () =>
  request({
    url: 'tuya.m.cl.combo.register',
    data: {}
  }).then((rep) => {
    if (!rep.success) {
      return {};
    }
    return rep.result;
  });

