import React, { FC, CSSProperties } from 'react'
import cls from 'classnames'
import styles from './index.module.scss'
import { View } from '@tarojs/components'

interface IBlockProps {
  disableGutters: boolean // 是否去掉padding
  className: string
  style: CSSProperties
  children?: any
}

// 模块样式
const Box: FC<Partial<IBlockProps>> = (props) => {
  const { children, className, style, disableGutters = false } = props
  const cl = cls(styles.block, className, { [styles.blockPadding]: !disableGutters })

  return (
    <View className={cl} style={style}>
      {children}
    </View>
  )
}

export default Box
