import React, { FC, useLayoutEffect, useRef } from 'react'
import styles from './index.module.scss'
import closeImg from '@/img/sb@2x.png'
import successImg from '@/img/success@2x.png'
import { Image, View } from '@tarojs/components'

interface IProps {
  status: boolean
  success: {
    text: string
  }
  error: {
    text: string
  }
  visible: boolean
  onClose: () => void
}


const Cover: FC<IProps> = ({ status, visible, success, error, onClose, ...props }) => {
  const timer = useRef<any>(null)

  useLayoutEffect(() => {
    if (visible) {
      // eslint-disable-next-line @typescript-eslint/no-unused-vars
      timer.current = setTimeout(() => {
        onClose()
      }, 1000)
    }

    return () => {
      timer.current && clearTimeout(timer.current)
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [visible])

  return (
    (visible ? (
      <View className={styles.bg} {...props}>
        <View className={styles.msg}>
          <Image src={!status && closeImg || successImg} className={styles.img} />
          <View>{!status ? error?.text.replace(/\s/g, ' ') : success?.text.replace(/\s/g, ' ')}</View>
        </View>
      </View>
    ) :
      null
    )
  )

}

export default Cover
