import React from 'react';
import BaseComponentProps from '@/common/types/BaseComponentProps';
import { View } from '@tarojs/components';
import styles from './index.module.scss';
import BaseOperateFooter, { BaseOperateFooterProps } from '../BaseOperateFooter';

interface IBasePicker extends BaseComponentProps {
  footerConfig?: BaseOperateFooterProps
}

const BasePage: React.FC<IBasePicker> = (props) => {
  const { children, className, style, footerConfig } = props
  const {
    className: footerClass,
    primaryButtonText = "",
    onPrimaryButtonClick = () => void 0,
    ...footer
  } = footerConfig || {}

  return <View className={`${styles.basePage} ${className}`} style={style}>
    {children}
    {footerConfig && <BaseOperateFooter
      className={`${styles.footer} ${footerClass}`}
      primaryButtonText={primaryButtonText}
      onPrimaryButtonClick={onPrimaryButtonClick}
      {...footer}
    />}
  </View>
};

export default BasePage;
