import React, { FC, useMemo } from 'react'
import cls from 'classnames'
import _ from 'lodash'
import { TimeLeft, TimeLeftTip, remainingTime } from './Time'
import { Image, View } from '@tarojs/components';
import styles from './index.module.scss'
import Device from './Device';

interface IProps {
  data: {
    name: string
    expireSec: number
    url: string
    version: string
    desc: string
    comboName: string
    merchantName: string
    oem: boolean
    deviceCount: number
    projectCount: number
    remainingProjectCount: number
    remainingDeviceCount: number
    comboSingleDetailVO: {
      version: string
      [key: string]: any
    }
    versionInfo: any
    edition: number
    [key: string]: any
  }
  className: string
}

const ComboHead: FC<IProps> = ({ data, className, ...props }) => {
  const { comboSingleDetailVO, expireSec, url, merchantName, edition = 0, oem } = data || {}

  const { version } = comboSingleDetailVO || {}

  const time = remainingTime(expireSec)

  const versionList = useMemo(() => data?.versionInfo ? data?.versionInfo : [
    {
      versionName: comboSingleDetailVO?.name,
      versionCode: comboSingleDetailVO?.version
    }
  ], [data, comboSingleDetailVO])

  return (
    <View>
      <View className={cls(styles.version, className)} {...props}>

        {!oem && time < 8 && (
          <TimeLeft edition={edition} timestamp={expireSec} />
        )}

        <View className={`${styles.content}`}>
          <View>
            {/* 账户名称 */}
            <View className={styles.title}>{merchantName}</View>
            {!data?.oem && (
              <View>

                {/* 套餐版本列表 */}
                <View className={styles.versionBox}>
                  {versionList.map((item) => <View key={item.versionCode} className={`${styles.comboName}`}>
                    {item?.versionName}
                  </View>)}
                </View>

                <TimeLeftTip
                  className={cls(styles.tipWrap)}
                  style={{ background: 'none' }}
                  timestamp={expireSec}
                  version={version}
                >
                </TimeLeftTip>
              </View>
            )}
          </View>

          {(!data?.oem && <Image src={url} className={styles.versionImg} />) || null}
        </View>
      </View>
      <Device data={data} />
    </View>
  )
}

export default ComboHead
