/** 获取当前环境的语言， 默认zh */
export const getLanguage = () => {
  const reg = /[-|_]/g;
  const infos = ty.getSystemInfoSync();
  const language = infos?.language?.split(reg)?.[0];
  console.log('language', infos?.language?.split(reg), language, infos);

  return ['zh', 'en', 'ja', 'es', 'de', 'fr'].includes(language) ? language : 'en';
};

export const isChinaArea = () => {
  const reg = /[-|_]/g;
  const infos = ty.getSystemInfoSync();
  const language = infos?.language?.split(reg)?.[0];

  return language === 'zh';
};
