import React, { FC } from 'react'
import cls from 'classnames'
import styles from './index.module.scss'
import { View } from '@tarojs/components'

interface IProps {
  disableGutters?: boolean
  className?: string
  children?: any
}

const Container: FC<IProps> = ({ disableGutters = false, children, className, ...props }) => {
  const cl = cls({ [styles.container]: !disableGutters }, className)

  return (
    <View className={cl} {...props}>
      {children}
    </View>
  )
}

export default Container
