import React, { FC } from 'react';
import Box from '@/components/Box';
import i18ns from '@/utils/i18n';
import { View } from '@tarojs/components';
import _ from 'lodash';
import styles from './index.module.scss';

interface Iprops {
  data: {
    deviceCount: number;
    projectCount: number;
    remainingProjectCount: number;
    remainingDeviceCount: number;
    [key: string]: any;
  };
}

const Device: FC<Iprops> = ({ data }) => {
  const {
    // deviceCount,
    // projectCount,
    remainingProjectCount,
    remainingDeviceCount
    // comboSingleDetailVO
  } = data || {};

  const t = i18ns();
  // const { version } = comboSingleDetailVO || {}

  const arr = [
    {
      title: t('remain.project.quota'),
      num: _.max([remainingProjectCount, 0])
      // maxNum: projectCount
    },
    {
      title: t('remain.device.quota'),
      num: _.max([remainingDeviceCount, 0])
      // maxNum: deviceCount
    }
  ];

  return (
    <Box className={styles.device}>
      {arr?.map(({ title, num }) => (
        <View className={styles.deviceItem} key={title + num}>
          <View className={styles.deviceTitle}>{title}</View>
          <View className={styles.deviceNum}>
            <View className={`${styles.num} ${!data?.oem}`}>{num}</View>
            {/* <View className={styles.maxNum}>/{maxNum}</View> */}
          </View>
        </View>
      ))}
    </Box>
  );
};

export default Device;
