
import React from 'react'
import Container from '@/components/container';
import i18n from '@/utils/i18n';
import styles from './index.module.scss'
import { Image, View } from '@tarojs/components';
import { isEmpty } from 'lodash';
import ordinary from '@/img/right-icon_oridinaty.png'
import advanced from '@/img/right-icon_advanced.png'
import profession from '@/img/right-icon_profession.png'
import { AtDivider } from 'taro-ui';

const VersionContent = ({ data, index: nthIndex }) => {
  const t = i18n();

  const imgMap = [ordinary, advanced, profession, profession]

  return (
    <Container className={styles.container}>
      <View className={`${styles.version}  ${styles[`version_${nthIndex}`]}`}>
        <View>
          <View className={styles.title}>{data?.name}</View>
          <View className={styles.desc}>{data?.desc.replace(/\s/g, ' ')}</View>
        </View>
        {/* <Image src={data?.url} className={styles.versionImg} /> */}
      </View>

      <AtDivider content={t('account')} fontColor='#838aa0' lineColor='#e9ecef'></AtDivider>

      <View className={styles.itemList}>
        {data?.equityList?.length ?
          data?.equityList?.map((item) =>
          (
            <View key={item.contentId} className={styles.column}>
              <View className={styles.title}>{item.content}</View>
              <View className={styles.value}>
                {(!isEmpty(item.value) && item.value) || (
                  <Image className={styles.right} src={imgMap[nthIndex]} />
                )}
              </View>
            </View>
          )
          ) : null}
      </View>
    </Container>
  )
}

export default VersionContent
