import React, { useLayoutEffect, useState } from 'react'
import i18n from '@/utils/i18n';
import { Checkbox, CheckboxGroup, Image, View } from '@tarojs/components'
import styles from './index.module.scss'
import Taro, { getCurrentInstance } from '@tarojs/taro'
import BasePage from '@/components/BasePage';
import checkIcon from '@/common/img/check.png';
import checkActIcon from '@/common/img/check-act.png';
import { getAddValueProjectList, submitAddValueProject } from '@/api/index';
import SearchBar from '@/components/SearchBar';

export interface IListItem {
  projectName: string
  projectType: 0 | 1 | 2
  status: number
  vasProjectId: string
}


const ProjectList = () => {
  const t = i18n();

  const { router } = getCurrentInstance()
  const [list, setList] = useState<IListItem[]>([])
  const [checked, setChecked] = useState<string[]>([])
  const [searchVal, setSearchVal] = useState<string>('')

  const getProjectList = async () => {
    Taro.showLoading({ title: t('loading') })
    const res = await getAddValueProjectList({
      params: JSON.stringify({
        vasId: router?.params?.id,
        page: 1,
        pageSize: 10
      })
    })
    console.log('getAddValueProjectList', res)
    setList(res?.list)
    Taro.hideLoading()
  }

  useLayoutEffect(() => {
    Taro.setNavigationBarTitle({ title: t('chooseProject') })
    getProjectList()
  }, [])

  /** 选择提交 */
  const onPrimaryButtonClick = async () => {
    if (!checked?.length) {
      Taro.showToast({ title: t('chooseProjectTips') })
      return
    }
    Taro.showLoading({ title: t('submitLoading') })
    const res = await submitAddValueProject({
      params: JSON.stringify({
        projectId: checked,
        vasId: router?.params?.id
      })
    })
    Taro.hideLoading()
    res && Taro.navigateBack()
  }

  const handleGroupChange = (v) => {
    setChecked(v?.detail?.value)
  }

  const CheckIcon = (item: IListItem) => checked?.includes(item?.vasProjectId)
    ? <Image className={styles.checkIcon} src={checkActIcon}></Image>
    : <Image className={styles.checkIcon} src={checkIcon}></Image>

  return <BasePage
    className={styles.projectList}
    footerConfig={{
      primaryButtonText: t('ok'),
      onPrimaryButtonClick
    }}
  >
    <SearchBar
      placeholder={t('searchPlaceholder')}
      searchOnChange={1}
      searchOnClear
      fixed
      onSearch={(v) => {
        setChecked([])
        setSearchVal(v)
      }}
    ></SearchBar>

    <View className={styles.list}>
      <CheckboxGroup onChange={handleGroupChange}>
        {list?.filter((item) => !searchVal || item?.projectName?.includes(searchVal))?.map((v, index) => (<View key={v?.vasProjectId} className={`${styles.listItem} ${(index === (list?.length - 1)) ? styles.mb56 : null}`}>
          <View className={`${styles.check}`}>
            {CheckIcon(v)}
            <Checkbox value={v?.vasProjectId}></Checkbox>
          </View>
          <View className={styles.groupName}>{v?.projectName}</View>
        </View>))}
      </CheckboxGroup>
    </View>

  </BasePage>
}

export default ProjectList
